body {
  font-family: 'Poppins', sans-serif;
  margin: 20px;
}

.App {
  width: 100%;
  text-align: center;
}

.main {
  display: inline-block;
  margin: 0 auto;
  padding: 3px;
  text-align: center;
}

.main-title{
  font-size: 30px;
  letter-spacing: 1px;
}

.board-row:after {
  clear: both;
  content: "";
  display: table;
}

.status {
  margin-top: 15px;
  margin-bottom: 15px;
  font-size: 25px;
  letter-spacing: 1px;
}

.square {
  background: #fff;
  border: 4px solid rgb(0, 0, 0);
  float: left;
  font-size: 80px;
  font-weight: bold;
  line-height: 50px;
  height: 134px;
  margin-right: -1px;
  margin-top: -1px;
  padding: 0;
  text-align: center;
  width: 134px;
}

@media only screen and (max-width: 600px) {
  .square {
    width: 80px;
    height: 80px;
    font-size: 60px;
    
  }
}

.reset-btn {
  background-color: black;
  color: white;
  font-size: 20px;
  padding: 10px 45px;
  border-radius: 40px;
  margin: 10px 0px;
  cursor: pointer;
  
}